
.calendar {
  background: #F5F5F5;
}
.calendar main .wrapper {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    overflow: auto;
    white-space: nowrap;
}
.calendar main .wrapper .day {
      margin: 0;
      flex-shrink: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      /*text-align: center;*/
      cursor: pointer;
}
.calendar main .wrapper .day .icon {
        font-size: 2em;
        /*margin: 0.5em 0 0 0;*/
        padding: 0 0 0.25em 0;
        margin: auto auto;
}
.calendar main .wrapper .day p {
        font-size: 1.2em;
        padding: 0.25em 0 0 0;
        margin: auto auto;
}
.calendar main .wrapper .day .active {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        background: #3F51B5;
        color: #F5F5F5;
        width: 30vw;
        height: 30vw;
        /*padding: 0.5em;*/
}
.calendar main .wrapper .day .not-active {
        /*display: flex;
                    flex-direction: column;
                    justify-content: space-around;*/
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        background: none;
        width: 30vw;
        height: 30vw;
        /*padding: 0.5em;*/
}



