
.day-night {
  width: 100%;
  height: auto;
  background: #F5F5F5;
}
.day-night main {
    margin: 1em 2em 1em 2em;
}
.day-night main .visualisation {
      width: 100%;
      height: 14px;
      border-radius: 7px;
      background: #3F51B5;
}
.day-night main .visualisation .day {
        width: 55%;
        height: 100%;
        border-radius: 7px;
        background: #F5F5F5;
        margin-left: 20%;
}
.day-night main .visualisation .day .current {
          width: 14px;
          height: 14px;
          border-radius: 7px;
          background: #FFC107;
          margin-left: 50%;
}
.day-night main .description {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
}
.day-night main .description .value {
        font-size: 1em;
}
.day-night main .description .key {
        font-size: 0.8em;
}
.day-night main .description .desc p {
        margin: 5px 0 5px 0;
        padding: 0;
}
.day-night main .description .start {
        text-align: left;
}
.day-night main .description .end {
        text-align: right;
}



