
.menu-bar {
  width: 100%;
  height: auto;
  background: #F5F5F5;
  box-shadow: 0 1px 0 #BDBDBD;
  position: fixed;
}
.menu-bar main {
    margin: 1em 1em 1em 1em;
    display: flex;
    flex-direction: row;
}
.menu-bar main .back-arrow {
      width: 20px;
      height: 20px;
      margin: 0;
      padding: 0;
      background: url("../../../assets/back-arrow2.svg");
      background-size: contain;
      background-repeat: no-repeat;
      border: none;
}
.menu-bar main .router {
      color: black;
      margin: 0;
      display: flex;
      justify-content: center;
}
.menu-bar main .description {
      margin: auto 2em auto 0.5em;
      width: 100%;
      text-align: center;
}



